import axios from 'axios'
/* eslint-disable */
const url = 'api/aviations/'

class AviationService {
  static getAviationText () {
    return new Promise(function(resolve, reject) {
      axios.get(`${url}`).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }

  static createAviationText(text) {
    return new Promise(function(resolve, reject) {
      axios.post(`${url}`, { text: text }).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }

  static updateAviationText (id, text) {
    return new Promise(function(resolve, reject) {
      axios.put(`${url}${id}`, {
        text: text
      }).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }
}

export default AviationService