<template>
  <div>
    <div class="container-fluid">
      <div class="section-header-padding-top"></div>
      <div class="section-content-container">
        <h3 class="text-center">Helicopter/Aviation</h3>
      </div>
      <div id="aviationTextContainer" @click="toggleEdit()">
      </div>
      <div v-if="showEdit">
        <b-form>
          <b-form-group>
            <b-form-textarea
              v-model="newText"
              class="aviation-edit-height"
              v-bind:style="{ height: aviationTextHeight + 'px' }"
            ></b-form-textarea>
          </b-form-group> 
          <div class="text-center">     
            <b-button type="submit" @click.prevent="submitAviationText()" variant="primary">Submit</b-button>
            <b-button class="button-margin-left" type="submit" @click.prevent="toggleEdit()" variant="danger">Cancel</b-button>
          </div>
        </b-form>
      </div>
    </div>
    <div class="section-header-padding-bottom"></div>
  </div>
</template>

<script>
import AviationService from '../services/AviationService'

export default {
  name: 'EditAviationServices',
  data () {
    return {
      aviations: {},
      newText: "",
      showEdit: false,
      aviationTextHeight: "",
      newTextSubmitted: false
    }
	},
	methods: {
    toggleEdit() {
      if(!this.showEdit) {
        this.showEdit = true;
        this.aviationTextHeight = document.getElementById("aviationTextContainer").offsetHeight;
        document.getElementById("aviationTextContainer").innerHTML = "";
        const re = /<br>/g;
        this.newText = this.newText.replace(re, '\n');
      } else {
        this.showEdit = false;
        const re = /\n/g;
        if(this.newTextSubmitted) {
          this.aviation.text = this.newText;
        } 
        this.newText = this.aviation.text.replace(re, '<br>');
        document.getElementById("aviationTextContainer").innerHTML = this.newText;
        this.newTextSubmitted = false;
      }
    },
    submitAviationText() {
      let that = this;
      AviationService.updateAviationText(this.aviation._id, this.newText).then(function(result) {
        that.showEdit = false;
        that.newTextSubmitted = true;
        const re = /\n/g;
        that.newText = result.data.aviation.text.replace(re, '<br>');
        document.getElementById("aviationTextContainer").innerHTML = that.newText;
        return result;
      }).catch(function(err) {
        return err;
      });
    }
  },
  mounted() {
    var that = this;
    AviationService.getAviationText().then(function(result) {
      const re = /\n/g;
      that.aviation = result.data.aviations[0];
      that.newText = that.aviation.text.replace(re, '<br>');
      document.getElementById("aviationTextContainer").innerHTML = that.newText;
      return result;
    }).catch(function(err) {
      return err;
    });
  }
}
</script>

