<template>
  <div>
    <div class="container-fluid">
      <div class="section-header-padding-top"></div>
      <div class="section-content-container">
        <h3 class="text-center">Engineering Services</h3>
      </div>
      <div id="engineeringTextContainer" @click="toggleEdit()">
      </div>
      <div v-if="showEdit">
        <b-form>
          <b-form-group>
            <b-form-textarea
              v-model="newText"
              class="engineering-edit-height"
              v-bind:style="{ height: engineeringTextHeight + 'px' }"
            ></b-form-textarea>
          </b-form-group> 
          <div class="text-center">     
            <b-button type="submit" @click.prevent="submitEngineeringText()" variant="primary">Submit</b-button>
            <b-button class="button-margin-left" type="submit" @click.prevent="toggleEdit()" variant="danger">Cancel</b-button>
          </div>
        </b-form>
      </div>
    </div>
    <div class="section-header-padding-bottom"></div>
  </div>
</template>

<script>
import EngineeringService from '../services/EngineeringService'

export default {
  name: 'EditEngineeringServices',
  data () {
    return {
      engineerings: {},
      newText: "",
      showEdit: false,
      engineeringTextHeight: "",
      newTextSubmitted: false
    }
	},
	methods: {
    toggleEdit() {
      if(!this.showEdit) {
        this.showEdit = true;
        this.engineeringTextHeight = document.getElementById("engineeringTextContainer").offsetHeight;
        document.getElementById("engineeringTextContainer").innerHTML = "";
        const re = /<br>/g;
        this.newText = this.newText.replace(re, '\n');
      } else {
        this.showEdit = false;
        const re = /\n/g;
        if(this.newTextSubmitted) {
          this.engineering.text = this.newText;
        } 
        this.newText = this.engineering.text.replace(re, '<br>');
        document.getElementById("engineeringTextContainer").innerHTML = this.newText;
        this.newTextSubmitted = false;
      }
    },
    submitEngineeringText() {
      let that = this;
      EngineeringService.updateEngineeringText(this.engineering._id, this.newText).then(function(result) {
        that.showEdit = false;
        that.newTextSubmitted = true;
        const re = /\n/g;
        that.newText = result.data.engineering.text.replace(re, '<br>');
        document.getElementById("engineeringTextContainer").innerHTML = that.newText;
        return result;
      }).catch(function(err) {
        return err;
      });
    }
  },
  mounted() {
    var that = this;
    EngineeringService.getEngineeringText().then(function(result) {
      const re = /\n/g;
      that.engineering = result.data.engineerings[0];
      that.newText = that.engineering.text.replace(re, '<br>');
      document.getElementById("engineeringTextContainer").innerHTML = that.newText;
      return result;
    }).catch(function(err) {
      return err;
    });
  }
}
</script>

