import axios from 'axios'
/* eslint-disable */
const url = 'api/yacht-managements/'

class YachtManagementService {
  static getYachtManagementText () {
    return new Promise(function(resolve, reject) {
      axios.get(`${url}`).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }

  static updateYachtManagementText (id, text) {
    return new Promise(function(resolve, reject) {
      axios.put(`${url}${id}`, {
        text: text
      }).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }
}

export default YachtManagementService