<template>
  <div>
    <div class="section-padding">
      <a name="engineering_services"></a>
      <div class="section-header-padding-top"></div>
      <h1 class="text-center section-header">Engineering Services</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="row section-content-container">
        <div class="col-md-6 col-sm-6 col-xs-12">
          <div id="engineeringText" class="text-container"></div>
        </div>
        <div class="col-md-6 col-sm-6 col-xs-12">
          <div class="image-container">
            <img src="../assets/images/engineering.jpg" class="section-img" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EngineeringService from '../services/EngineeringService'

export default {
  name: 'EngineeringServices',
  data() {
    return {
      engineeringText: ""
    }
  },
  mounted() {
    var that = this;
    EngineeringService.getEngineeringText().then(function(result) {
      const re = /\n/g;
      that.engineeringText = result.data.engineerings[0].text.replace(re, '<br>');
      document.getElementById("engineeringText").innerHTML = that.engineeringText;
      return result;
    }).catch(function(err) {
      return err;
    });
  }
}
</script>
