<template>
  <div>
    <div class="container-fluid">
      <div class="section-header-padding-top"></div>
      <div class="section-content-container">
        <h3 class="text-center">Yacht Management</h3>
      </div>
      <div id="yachtManagementTextContainer" @click="toggleEdit()">
      </div>
      <div v-if="showEdit">
        <b-form>
          <b-form-group>
            <b-form-textarea
              v-model="newText"
              class="yacht-edit-height"
              v-bind:style="{ height: yachtTextHeight + 'px' }"
            ></b-form-textarea>
          </b-form-group> 
          <div class="text-center">     
            <b-button type="submit" @click.prevent="submitYachtManagementText()" variant="primary">Submit</b-button>
            <b-button class="button-margin-left" type="submit" @click.prevent="toggleEdit()" variant="danger">Cancel</b-button>
          </div>
        </b-form>
      </div>
    </div>
    <div class="section-header-padding-bottom"></div>
  </div>
</template>

<script>
import YachtManagementService from '../services/YachtManagementService'

export default {
  name: 'EditYachtManagement',
  data () {
    return {
      yachtManagement: {},
      newText: "",
      showEdit: false,
      yachtTextHeight: "",
      newTextSubmitted: false
    }
	},
	methods: {
    toggleEdit() {
      if(!this.showEdit) {
        this.showEdit = true;
        this.yachtTextHeight = document.getElementById("yachtManagementTextContainer").offsetHeight;
        document.getElementById("yachtManagementTextContainer").innerHTML = "";
        const re = /<br>/g;
        this.newText = this.newText.replace(re, '\n');
      } else {
        this.showEdit = false;
        const re = /\n/g;
        if(this.newTextSubmitted) {
          this.yachtManagement.text = this.newText;
        } 
        this.newText = this.yachtManagement.text.replace(re, '<br>');
        document.getElementById("yachtManagementTextContainer").innerHTML = this.newText;
        this.newTextSubmitted = false;
      }
    },
    submitYachtManagementText() {
      let that = this;
      YachtManagementService.updateYachtManagementText(this.yachtManagement._id, this.newText).then(function(result) {
        that.showEdit = false;
        that.newTextSubmitted = true;
        const re = /\n/g;
        that.newText = result.data.yachtmanagement.text.replace(re, '<br>');
        document.getElementById("yachtManagementTextContainer").innerHTML = that.newText;
        return result;
      }).catch(function(err) {
        return err;
      });
    }
  },
  mounted() {
    var that = this;
    YachtManagementService.getYachtManagementText().then(function(result) {
      const re = /\n/g;
      that.yachtManagement = result.data.yachtmanagements[0];
      that.newText = that.yachtManagement.text.replace(re, '<br>');
      document.getElementById("yachtManagementTextContainer").innerHTML = that.newText;
      return result;
    }).catch(function(err) {
      return err;
    });
  }
}
</script>

