import axios from 'axios'
/* eslint-disable */
const url = 'api/engineerings/'

class EngineeringService {
  static getEngineeringText () {
    return new Promise(function(resolve, reject) {
      axios.get(`${url}`).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }

  static createEngineeringText(text) {
    return new Promise(function(resolve, reject) {
      axios.post(`${url}`, { text: text }).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }

  static updateEngineeringText (id, text) {
    return new Promise(function(resolve, reject) {
      axios.put(`${url}${id}`, {
        text: text
      }).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }
}

export default EngineeringService