<template>
  <div>
    <div class="section-padding">
      <a name="helicopter_aviation"></a>
      <div class="section-header-padding-top"></div>
      <h1 class="text-center section-header">Helicopter/Aviation</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="row section-content-container">
        <div class="col-md-6 col-sm-6 col-xs-12">
          <div class="image-container">
            <img src="../assets/images/aviation.jpg" class="section-img">
          </div>
        </div>
        <div class="col-md-6 col-sm-6 col-xs-12">
          <div id="aviationText" class="text-container"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AviationService from '../services/AviationService'

export default {
  name: 'Aviation',
  data () {
    return {
      aviationText: ""
    }
  },
  mounted() {
    var that = this;
    AviationService.getAviationText().then(function(result) {
      const re = /\n/g;
      that.aviationText = result.data.aviations[0].text.replace(re, '<br>');
      document.getElementById("aviationText").innerHTML = that.aviationText;
      return result;
    }).catch(function(err) {
      return err;
    });
  }
}
</script>
