import Vue from 'vue'
import App from './App.vue'
import Router from 'vue-router'
import Public from '@/components/Public'
import Admin from '@/components/Admin'
import Login from '@/components/Login'
import EditBlog from '@/components/EditBlog'
import EditCompanyProfile from '@/components/EditCompanyProfile'

import BootstrapVue from 'bootstrap-vue'
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { fas } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(fab)
library.add(fas)
Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.use(BootstrapVue)

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import './assets/stylesheets/updated.scss'
import './assets/javascript/application.js'
import axios from 'axios'

Vue.config.productionTip = false

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Public',
      component: Public
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin,
      meta: { 
        requiresAuth: true,
        isAdmin : true
      }
    },
    {
      path: '/edit-blog',
      name: 'EditBlog',
      component: EditBlog,
      meta: { 
        requiresAuth: true,
        isAdmin : true
      }
    },
    {
      path: '/edit-company-profile',
      name: 'EditCompanyProfile',
      component: EditCompanyProfile,
      meta: { 
        requiresAuth: true,
        isAdmin : true
      }
    }
  ]
});

// axios request to middleware to check valid jwt
const validateJwt = function() {
  return new Promise(function(resolve, reject) {
    axios.get('/api/auth').then(function(result) {
      console.log("hello");
      resolve(result);
    }).catch(function(err) {
      reject(err);
    });
  });
}

// change requiresAuth to requiresOnline and make a check to jwt token, block urls like profile
router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if(localStorage.getItem('user') !== null) {
      if(localStorage.getItem('jwt')) {
        let token = localStorage.getItem('jwt');
        axios.defaults.headers.common = {'Authorization': `Bearer ${token}`};
        validateJwt().then(function(result) {
          return result;
        }).catch(function() {
          localStorage.clear();
          next({
            path: '/login',
            params: { nextUrl: to.fullPath }
          });
        });
      }
      next();
    } else {
      if(localStorage.getItem('jwt') == null) {
        next({
          path: '/login',
          params: { nextUrl: to.fullPath }
        });
      }
    }   
  } else {
    next(); 
  }
});

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})
