<template>
  <div>
    <div class="section-padding">
      <a name="company_profile"></a>
      <div class="section-header-padding-top"></div>
      <h1 class="text-center section-header">Company Profile</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="company-profile-container">
        <div class="row section-content-container">
          <div id="employeeCard" class="col-md-4 col-sm-12 col-xs-12" v-for="employee in employees.slice(indexStart, indexEnd)" v-bind:key="employee._id">
            <div class="card text-center" v-b-modal="`modal-${employee._id}`">
              <img v-if="employee.local_photo_name" :src="getImgUrl(employee.local_photo_name)" class="card-img-top" />
              <img v-if="!employee.local_photo_name" :src="`https://s3-us-west-1.amazonaws.com/oceans-yachting/${employee.photo_name}`" class="card-img-top" />
              <div class="card-body">
                <h5 class="card-title">{{employee.first_name}} {{employee.last_name}}</h5>
                <p class="container-fluid card-text">{{employee.title}}</p>
              </div>
            </div>
            <b-modal v-bind:id="`modal-${employee._id}`">
              <div slot="modal-title">
                {{employee.first_name}} {{employee.last_name}}
                <p>{{employee.title}}</p>
              </div>
              <div>
                <p>{{employee.bio}}</p>
                <p v-if="employee.quote != ''">"{{employee.quote}}"</p>
                <div v-for="bulletPoint in employee.bullet_points" v-bind:key="bulletPoint._id">
                  <ul v-if="employee.bullet_points.length >= 1 && employee.bullet_points[0] != ''">
                    <li>{{bulletPoint}}</li>
                  </ul>
                </div>
              </div>
              <div slot="modal-footer" class="w-100 modal-close-btn">
                <b-button
                  size="sm"
                  class="float-right main-background-color"
                  @click="show=false"
                >
                  Close
                </b-button>
              </div>
            </b-modal>
          </div>
          <a class="prev" @click="prevRow()">
            <font-awesome-icon :icon="{ prefix: 'fas', iconName: 'angle-left' }" />
          </a>
          <a class="next" @click="nextRow()">
            <font-awesome-icon :icon="{ prefix: 'fas', iconName: 'angle-right' }" />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import EmployeeService from '../services/EmployeeService'

export default {
  name: 'CompanyProfile',
  data () {
    return {
      employees: [],
      firstName: null,
      lastName: null,
      title: null,
      bio: null,
      quote: null,
      bulletPoints: null,
      photo: null,
      errors: '',
      addEmployee: EmployeeService.addEmployee,
      slide: 0,
      sliding: null,
      currentRow: 1,
      indexStart: 0,
      indexEnd: 3,
      isLastRow: false,
      numRows: 0,
      additionalRow: 0,
      finalIndexRemainder: 0,
      isMobileScreen: false
    }
  },
  created() {
    let that = this;
    EmployeeService.getEmployees().then(function(result) {
      that.employees = result;
      that.getInitialScreen();
      that.calculateView();
    }).catch(function(err) {
      console.log(err);
    });
  },
  destroyed() {
    window.removeEventListener("resize", this.checkScreenSize);
  },
  methods: {
    getInitialScreen() {
      if(document.documentElement.clientWidth < 873) {
        this.isMobileScreen = true;
      } else {
        this.isMobileScreen = false;
      }
      this.calculateRows(this.employees);
    },
    checkScreenSize() {
      if(document.documentElement.clientWidth < 873) {
        this.isMobileScreen = true;
      } else {
        this.isMobileScreen = false;
      }
      this.calculateRows(this.employees);
    },
    getImgUrl(imgName) {
      return require('../assets/images/' + imgName);
    },
    calculateView() {
      window.addEventListener("resize", this.checkScreenSize);
    },
    calculateRows(employeeArray) {
      if(this.isMobileScreen) {
        this.numRows = employeeArray.length;
        this.rowRemainder = false;
        this.indexStart = 0;
        this.indexEnd = 1;
      } else {
        this.indexStart = 0;
        this.indexEnd = 3;
        if(employeeArray.length % 3 === 0) {
          this.numRows = employeeArray.length / 3;
          this.rowRemainder = false;
        } else {
          this.numRows = Math.trunc(employeeArray.length /3) + 1;
          this.finalIndexRemainder = employeeArray.length % 3;
          this.rowRemainder = true;
        }
      }
    },
    nextRow() {
      this.currentRow += 1;
      this.additionalRow += 1;
      
      if(this.isMobileScreen) {
        if(this.currentRow <= this.numRows) {
          this.indexStart = this.additionalRow;
          this.indexEnd = this.additionalRow + 1;
        }

        if(this.currentRow === this.numRows) {
          this.isLastRow = true;
        }

        if(this.currentRow > this.numRows) {
          this.isLastRow = false;
          this.indexStart = 0;
          this.indexEnd = 1;
          this.currentRow = 1;
          this.additionalRow = 0;
        }
      } else {
        if(this.currentRow < this.numRows) {
          this.indexStart = this.additionalRow * 3;
          this.indexEnd = (this.additionalRow * 3) + 3;  
        }

        if(this.currentRow === this.numRows) {
          this.isLastRow = true;
          this.indexStart = this.additionalRow * 3;
          if(this.rowRemainder) {
            this.indexEnd = (this.additionalRow * 3) + this.finalIndexRemainder;
          } else {
            this.indexEnd = (this.additionalRow * 3) + 3;  
          }
        }

        if(this.currentRow > this.numRows) {
          this.isLastRow = false;
          this.indexStart = 0;
          this.indexEnd = 3;
          this.currentRow = 1;
          this.additionalRow = 0;
        }
      }
    },
    prevRow() {   
      if(this.isMobileScreen) {
        if(this.currentRow === 1) {
          this.indexStart = this.numRows - 1;
          this.indexEnd = this.indexStart + 1;
          this.currentRow = this.numRows;
        } else if(this.currentRow === 2) {
          this.indexStart = 0;
          this.indexEnd = 1;
          this.currentRow = 1;
        } else {
          this.indexStart -= 1;
          this.indexEnd -= 1;
          this.currentRow -= 1;
        }
      } else {
        if(this.currentRow === 1) {
          this.currentRow = this.numRows;
          this.additionalRow = this.numRows - 1;
        } else {
          this.currentRow -= 1;
          this.additionalRow -= 1;
        }

        if(this.currentRow < this.numRows) {
          this.indexStart = this.additionalRow * 3;
          this.indexEnd = (this.additionalRow * 3) + 3;  
        }

        if(this.currentRow === this.numRows) {
          this.isLastRow = true;
          this.indexStart = this.additionalRow * 3;
          if(this.rowRemainder) {
            this.indexEnd = (this.additionalRow * 3) + this.finalIndexRemainder;
          } else {
            this.indexEnd = (this.additionalRow * 3) + 3;  
          }
        }

        if(this.currentRow > this.numRows) {
          this.isLastRow = false;
          this.indexStart = 0;
          this.indexEnd = 3;
          this.currentRow = 1;
          this.additionalRow = 0;
        }
      }
    },
  }
}
</script>
