<template>
  <div>
    <div class="section-padding">
      <a name="yacht_management"></a>
      <div class="section-header-padding-top"></div>
      <h1 class="text-center section-header">Yacht Management</h1>
      <div class="section-header-underline"></div>
      <div class="section-header-padding-bottom"></div>
      <div class="row section-content-container">
        <div class="col-md-6 col-sm-6 col-xs-12">
          <div class="image-container">
            <img src="../assets/images/yacht-management.jpg" class="section-img" />
          </div>
        </div>
        <div class="col-md-6 col-sm-6 col-xs-12">
          <div id="yachtManagementText" class="text-container"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import YachtManagementService from '../services/YachtManagementService'

export default {
  name: 'YachtManagement',
  data() {
    return {
      yachtManagementText: ""
    }
  },
  mounted() {
    var that = this;
    YachtManagementService.getYachtManagementText().then(function(result) {
      const re = /\n/g;
      that.yachtManagementText = result.data.yachtmanagements[0].text.replace(re, '<br>');
      document.getElementById("yachtManagementText").innerHTML = that.yachtManagementText;
      return result;
    }).catch(function(err) {
      return err;
    });
  }
}
</script>
